.editor-styles-wrapper .ai-block {
	position: relative;

	&:before {
		border-radius: 6px;
		box-shadow: 0 5px 20px -10px currentColor;
		content: "";
		display: block;
		inset: 2px -20px;
		pointer-events: none;
		position: absolute;
		opacity: 0.1;
		transition: box-shadow 0.333s ease-in;
	}

	&:hover,
	&:focus,
	&:focus-within {

		&:before {
			box-shadow: 0 5px 20px 0px currentColor;
		}
	}

	p {
		width: 100%;
		padding: 1em 0;
		margin: 0;
	}

	button {
		transition: opacity 0.33s ease-in;


		svg {
			transform: scale(.8) rotate(45deg) translateY(2px) translateX(-2px);
		}

		&.is-busy {
			animation: none;
			background: none;
			position: relative;

			&:before {
				content: "";
				display: block;
				position: absolute;
				inset: 0;
				animation: components-button__loading-animation 5s linear infinite;
				opacity: 1;
				background-size: 100px 100%;
				background-image: linear-gradient(90deg,#fff, rgba(255,255,255,0.5) 25%,transparent 50%,#fff 0);
			}

			svg {
				transform: none;
			}
		}
	}

	input.components-text-control__input {
		padding: 0;
		border: 0;
		font-size: initial;
		color: inherit;
		background: transparent;
		line-height: 1;
		margin: 0 !important;
		height: auto;
		width: auto;
	}
}

@keyframes components-button__loading-animation {
	0% {
		background-position: -150px 0
	}
}


[data-type="blockify/ai-block"] {
	margin-bottom: var(--wp--style--block-gap, 1em);

	&.is-highlighted {
		box-shadow: none;
	}

	&:focus:after {
		display: none;
	}
}

.ai-block-settings {
	padding: 0 16px 16px;

	.components-flex {

		div {
			margin: 0;

			&:first-of-type {
				flex: 1;
			}
		}
	}
}

.ai-block-label {
	font-size: 11px;
	font-weight: 500;
	line-height: 1.4;
	text-transform: uppercase;
	display: inline-block;
	margin-bottom: calc(8px);
	padding: 0px;
}
